.profileContainer {
  height: 42.938rem;
}

.gRatio {
  display: flex;
  flex-direction: row;
  margin-left: 14.813rem;
  margin-top: min(5.813rem);
  margin-right: auto;
  column-gap: 4.313rem;
  align-items: center;
}

.gRatio div.logo {
  width: 41.313rem;
  max-height: 34.491rem;
  position: relative;
  left: 0;
}

.profileRight {
  display: flex;
  flex-direction: column;
  width: 23.7rem;
  margin-right: 5rem;
  align-items: center;
}

.imageContainer {
  width: 100%;
}

.imageContainer img {
  width: 90%;
}

.profileLinks {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.profileLinks a {
  position: relative;
}

.mediaIcons {
  display: flex;
  column-gap: 2.438rem;
  position: relative;
  left: -30px;
}

.mediaBtn {
  left: 0;
  width: 3.125rem;
  height: 3.125rem;
}

.linkBtn {
  left: 0;
  width: 8.563rem;
  height: 4.063rem;
  border-radius: 5px;
  background-color: #ebbab9;
  display: flex;
  justify-content: center;
  /* identical to box height */
  color: #bc393c;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
}

.profileRight p,
.linkBtn {
  font-family: "Open Sans";
  font-style: normal;
  /* identical to box height */
}

.profileRight p {
  font-weight: 600;
  line-height: 2.043rem;
  font-size: 1.5rem;
}

.profileRight p strong {
  color: #bc393c;
}
