.skillCardContainer {
  width: 67.8125rem;
  margin-left: 14.8125rem;
  border: 0.1875rem solid #4c8495;
  box-sizing: border-box;
  border-radius: 0.3125rem;
  display: flex;
  flex-direction: column;
  margin-bottom: 1.25rem;
}

.skillCardContainer h2 {
  margin-left: 1.25rem;
  color: #b5ffe1;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 2.25rem;
  line-height: 2.7425rem;
  width: 22.6875rem;
  margin-top: 0;
  margin-bottom: 0;
  text-align: start;
}

.skill {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 9.1875rem;
}

.skillList {
  display: flex;
  gap: 1.25rem 5.375rem;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 1.25rem;
}

.skill img {
  width: 6.125rem;
}

.skill p {
  margin: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.82875rem;
  width: 100%;
  text-align: center;
}
