.sideNav {
  height: 100%;
  width: 8.625rem;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #bc393c;
  transition: 0.5s ease;
  overflow-x: hidden;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
}

.logoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo {
  position: absolute;
  left: 1.23rem;
  right: auto;
  top: 3.56%;
  bottom: 89.3%;
}

a {
  position: absolute;
  left: 2.718rem;
  right: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
}

a.home {
  top: 21.67%;
  bottom: 73.89%;
}

a.about {
  top: 35.67%;
  bottom: 59.89%;
}

a.skills {
  top: 49.67%;
  bottom: 46.78%;
}

a.projects {
  top: 62.78%;
  bottom: 32.78%;
}

a.contact {
  top: 76.78%;
  bottom: 18.78%;
}

.sideNav:hover {
  width: 14.813rem;
}

.sideNav:hover a {
  left: 2.718rem;
  right: auto;
}

a p {
  position: absolute;
  left: 4rem;

  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 28px;
  /* identical to box height */
  color: #bc393c;
  transition: all 0.2s ease-in;
}

.logoOctavio,
.logoVillena {
  position: absolute;
  right: auto;

  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  /* identical to box height */

  text-align: center;

  color: #bc393c;
  transition: all 0.2s ease-in;
}

p.logoOctavio {
  left: 7.063rem;
  top: 1.563rem;
}

p.logoVillena {
  left: 7.125rem;
  top: 2.813rem;
  letter-spacing: 0.04em;
}

.sideNav:hover a p {
  color: #fff;
}

.sideNav:hover .logoOctavio,
.sideNav:hover .logoVillena {
  color: #08415c;
}

.sideNav:hover .logo {
  left: 1.25rem;
}
