@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #08415c;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Montserrat", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap");
  font-weight: bold;
}
.App {
  text-align: center;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sideNav {
  height: 100%;
  width: 8.625rem;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #bc393c;
  transition: 0.5s ease;
  overflow-x: hidden;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
}

.logoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo {
  position: absolute;
  left: 1.23rem;
  right: auto;
  top: 3.56%;
  bottom: 89.3%;
}

a {
  position: absolute;
  left: 2.718rem;
  right: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
}

a.home {
  top: 21.67%;
  bottom: 73.89%;
}

a.about {
  top: 35.67%;
  bottom: 59.89%;
}

a.skills {
  top: 49.67%;
  bottom: 46.78%;
}

a.projects {
  top: 62.78%;
  bottom: 32.78%;
}

a.contact {
  top: 76.78%;
  bottom: 18.78%;
}

.sideNav:hover {
  width: 14.813rem;
}

.sideNav:hover a {
  left: 2.718rem;
  right: auto;
}

a p {
  position: absolute;
  left: 4rem;

  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 28px;
  /* identical to box height */
  color: #bc393c;
  transition: all 0.2s ease-in;
}

.logoOctavio,
.logoVillena {
  position: absolute;
  right: auto;

  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  /* identical to box height */

  text-align: center;

  color: #bc393c;
  transition: all 0.2s ease-in;
}

p.logoOctavio {
  left: 7.063rem;
  top: 1.563rem;
}

p.logoVillena {
  left: 7.125rem;
  top: 2.813rem;
  letter-spacing: 0.04em;
}

.sideNav:hover a p {
  color: #fff;
}

.sideNav:hover .logoOctavio,
.sideNav:hover .logoVillena {
  color: #08415c;
}

.sideNav:hover .logo {
  left: 1.25rem;
}

.profileContainer {
  height: 42.938rem;
}

.gRatio {
  display: flex;
  flex-direction: row;
  margin-left: 14.813rem;
  margin-top: min(5.813rem);
  margin-right: auto;
  grid-column-gap: 4.313rem;
  -webkit-column-gap: 4.313rem;
          column-gap: 4.313rem;
  align-items: center;
}

.gRatio div.logo {
  width: 41.313rem;
  max-height: 34.491rem;
  position: relative;
  left: 0;
}

.profileRight {
  display: flex;
  flex-direction: column;
  width: 23.7rem;
  margin-right: 5rem;
  align-items: center;
}

.imageContainer {
  width: 100%;
}

.imageContainer img {
  width: 90%;
}

.profileLinks {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.profileLinks a {
  position: relative;
}

.mediaIcons {
  display: flex;
  grid-column-gap: 2.438rem;
  -webkit-column-gap: 2.438rem;
          column-gap: 2.438rem;
  position: relative;
  left: -30px;
}

.mediaBtn {
  left: 0;
  width: 3.125rem;
  height: 3.125rem;
}

.linkBtn {
  left: 0;
  width: 8.563rem;
  height: 4.063rem;
  border-radius: 5px;
  background-color: #ebbab9;
  display: flex;
  justify-content: center;
  /* identical to box height */
  color: #bc393c;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
}

.profileRight p,
.linkBtn {
  font-family: "Open Sans";
  font-style: normal;
  /* identical to box height */
}

.profileRight p {
  font-weight: 600;
  line-height: 2.043rem;
  font-size: 1.5rem;
}

.profileRight p strong {
  color: #bc393c;
}

.aboutContainer {
  height: 42.938rem;
}

.aboutPara {
  margin-top: 7.25rem;
  margin-left: 16.0625rem;
  margin-right: 7.375rem;
  text-align: start;
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 1.5rem;
  line-height: 2.0625rem;
  max-width: 66.5625rem;
  height: 28.1875rem;
}

.h1Container h1 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 3rem;
  line-height: 3.6875rem;
  width: 22.6875rem;
  height: 3.6875rem;
  margin-left: 23.4375rem;
  text-align: start;
}

.skillContainer {
  margin-bottom: 10rem;
}

.skillCardContainer {
  width: 67.8125rem;
  margin-left: 14.8125rem;
  border: 0.1875rem solid #4c8495;
  box-sizing: border-box;
  border-radius: 0.3125rem;
  display: flex;
  flex-direction: column;
  margin-bottom: 1.25rem;
}

.skillCardContainer h2 {
  margin-left: 1.25rem;
  color: #b5ffe1;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 2.25rem;
  line-height: 2.7425rem;
  width: 22.6875rem;
  margin-top: 0;
  margin-bottom: 0;
  text-align: start;
}

.skill {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 9.1875rem;
}

.skillList {
  display: flex;
  grid-gap: 1.25rem 5.375rem;
  gap: 1.25rem 5.375rem;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 1.25rem;
}

.skill img {
  width: 6.125rem;
}

.skill p {
  margin: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.82875rem;
  width: 100%;
  text-align: center;
}

.projectCardContainer {
  width: 1085px;
  margin-left: 237px;
  margin-bottom: 2.5rem;
}

.projectCardContainer h2 {
  margin-left: 20px;
  color: #b5ffe1;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 43.88px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: start;
}

.project {
  display: flex;
  grid-gap: 0 1.25rem;
  gap: 0 1.25rem;
}

.project img {
  width: 28.3125rem;
}

.projectInfo {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.projectInfo h3 {
  color: #b5ffe1;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 1.6875rem;
  line-height: 2.056875rem;
  margin-bottom: 0;
}

.projectInfo p {
  font-family: Open Sans;
  font-style: normal;
  font-size: 1.5rem;
  line-height: 2.0425rem;
  margin-top: 0;
}

.linkBtn {
  position: relative;
  height: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 0.3125rem;
  border-radius: 5px;
  background-color: #ebbab9;
  display: flex;
  justify-content: center;
  color: #bc393c;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
}

