.projectCardContainer {
  width: 1085px;
  margin-left: 237px;
  margin-bottom: 2.5rem;
}

.projectCardContainer h2 {
  margin-left: 20px;
  color: #b5ffe1;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 43.88px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: start;
}

.project {
  display: flex;
  gap: 0 1.25rem;
}

.project img {
  width: 28.3125rem;
}

.projectInfo {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.projectInfo h3 {
  color: #b5ffe1;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 1.6875rem;
  line-height: 2.056875rem;
  margin-bottom: 0;
}

.projectInfo p {
  font-family: Open Sans;
  font-style: normal;
  font-size: 1.5rem;
  line-height: 2.0425rem;
  margin-top: 0;
}

.linkBtn {
  position: relative;
  height: auto;
  width: fit-content;
  padding: 0 0.3125rem;
  border-radius: 5px;
  background-color: #ebbab9;
  display: flex;
  justify-content: center;
  color: #bc393c;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
}
